@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,700&display=swap);
body {
    margin: 0;
    background-color: red;
    width: 1000px;
}

.App {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: grid;
    grid-template-columns: 4fr 6fr;
}

.pageTitle {
    font-family: Raleway;
    font-size: 1.5rem;
    text-align: center;
}

.clock-box {
    width: 500px;
    height: 500px;
    box-shadow: -8px 8px 15px rgba(225, 0, 0, 0.25),
    20px 20px 20px rgba(0, 0, 0, 0.2),
    inset -8px -8px 15px rgba(225, 0, 0, 0.25),
    inset 20px 20px 20px rgba(0, 0, 0, 0.2);
    position: relative;
    border: 0.25px solid black;
}



